@import "../../global.scss";

.navbar{
  box-sizing: border-box;
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
  align-items: flex-end;
  & .input{
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    & input{
      width: 100%;

      border: 0;
      border-bottom: 1px solid #000;
      height: 30px;
      margin-left: 10px;
      font-size: 16px;
      padding-right: 20px;
    }
    & svg{
      position: absolute;
      right: 0px;
      width: 20px;
      height: 20px;
    }
  }
  & .btn_group{
    display: flex;
    & .icon{
      margin: 0 10px;
      background-color: $primary_card_color;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 4px;
      color: #fff;
      & > svg {
        width: 20px;
        height: 20px;
        &.refreshing{
          animation: turn 1s linear ;
        }
      }
      &:nth-last-child(1){
        margin-right: 0;
      }
      &:nth-last-child(2){
        & > svg {
          width: 30px;
          height: 30px;
          path{
            fill: white;
          }
        }
        
      }
      &.be_chosen{
        background-color:rgba(0, 0, 255, 0.65)
      }
      &.down{
        transform: rotate(180deg);
      }
    }
  }
 
}
input:focus{
    outline: none;
}
@keyframes turn {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
